.panel {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.panel h2 {
  color: #11ff00;
}
.panel :host {
  display: flex;
  flex-direction: column;
}
.panel h3 {
  margin-top: 0;
  margin-bottom: 10px;
}
.panel .toolbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
}
.panel .view {
  flex: 1;
  padding: 10px;
  padding-top: 0px;
  overflow-y: auto;
  overflow-x: hidden;
}
.panel .QQ {
  background: url('http://wpa.qq.com/pa?p=2:774177933:51') !important;
  width: 77px;
  height: 25px;
}
.panel .log {
  flex: 1;
  width: 100%;
  height: 170px;
  background: #252525;
  resize: none;
  color: #fd942b;
  border-color: #fd942b;
  outline: none;
  box-sizing: border-box;
}
.panel .margin0 {
  margin: 0 0 0 0;
}
.panel div.section {
  border-bottom: 1px solid #666;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.panel div.section:last-child {
  border-bottom: 0px;
}
.panel div.group {
  min-width: 420px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
.panel span {
  margin-right: 0.25em;
}
